<template>
  <div class="user-add">
    <el-form :model="form" :rules="rules" ref="form" label-width="80px">
      <el-col :span="11">
        <el-form-item label="会员编号" prop="userNo">
          <el-input v-model="form.userNo"></el-input>
        </el-form-item>
        <el-form-item label="真实姓名" prop="userRealName">
          <el-input v-model="form.userRealName"></el-input>
        </el-form-item>
        <el-form-item label="昵称" prop="userWxName">
          <el-input v-model="form.userWxName"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex">
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="地区">
          <el-cascader
            v-model="form.city"
            :options="cityOptions"
            :props="{value: 'label', checkStrictly: true}"
            filterable>
          </el-cascader>
        </el-form-item>
      </el-col>
      <el-col :span="11" :offset="2">
        <el-form-item label="车型" prop="carType">
          <el-cascader
            v-model="form.carType"
            :options="carTypeOptions"
            :props="{value: 'label', checkStrictly: true}"
            filterable
            >
          </el-cascader>
        </el-form-item>
        <el-form-item label="车色" prop="carColor">
          <el-select v-model="form.carColor">
            <el-option
              v-for="(item, index) in carColorOptions"
              :key="index"
              :label="item.label"
              :value="item.label">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="车牌号" prop="carNo">
          <el-input v-model="form.carNo"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="userState">
          <el-select v-model="form.userState">
            <el-option label="未领取" value="未领取"></el-option>
            <el-option label="未贴标" value="未贴标"></el-option>
            <el-option label="正常" value="正常"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
      </el-col>
    </el-form>
  </div>
</template>

<script>
import $axios from '@/plugins/ajax';
import city from '@/data/city';

export default {
  data() {
    return {
      cityOptions: city,

      form: {
        userNo: '',
        userRealName: '',
        userWxName: '',
        sex: '男',
        city: ['陕西', '西安'],
        carType: ['01'],
        carColor: '白色',
        carNo: '',
        userState: '未领取',
        phone: ''
      },
      rules: {
        userNo: [
          { required: true, message: '会员编号不能为空', trigger: 'blur' }
        ],
        userRealName: [
          { required: true, message: '真实姓名不能为空', trigger: 'blur' }
        ]
      }
    };
  },

  computed: {
    carTypeOptions() {
      return this.$store.state.carTypeOptions;
    },

    carColorOptions() {
      return this.$store.state.carColorOptions;
    }
  },

  methods: {
    postData() {
      return new Promise((resolve, reject) => {
        this.$refs['form'].validate((valid) => {
          if (valid) {
            $axios.post('/userAdd', this.form).then((res) => {
              if (res.data.result) {
                this.$message.success(res.data.message);
                resolve();
              } else {
                this.$message.error(res.data.message);
                reject(new Error('添加会员失败'));
              }
            }).catch((e) => {
              this.$message.error('服务器发生错误');
              reject(new Error('通讯失败'));
            });
          } else {
            reject(new Error('表单验证不通过'));
          }
        });
      });
    }
  }
};
</script>

<style>
  .user-add .el-select,
  .user-add .el-cascader {
    width: 100%;
  }
</style>
