<template>
  <div class="user-all">
    <span
      :class="`unit ${item.active? 'active' : ''}`"
      v-for="item in allId"
      :key="item.id">{{item.id}}</span>
  </div>
</template>

<script>
export default {
  props: {
    user: {
      type: Array,
      required: true
    }
  },

  data() {
    return {
      list: [],
      allId: []
    };
  },

  created() {
    this.process();
  },

  methods: {
    process() {
      this.list = JSON.parse(JSON.stringify(this.user));
      this.list.sort((first, second) => {
        return Number(first) - Number(second);
      });
      for (let i = 1, j = 0; i < 1000; i++) {
        if (i.toString().length === 1) {
          this.allId.push({ id: '00' + i, active: false });
        } else if (i.toString().length === 2) {
          this.allId.push({ id: '0' + i, active: false });
        } else {
          this.allId.push({ id: i.toString(), active: false });
        }
        if (this.list[j] && this.allId[i - 1].id === this.list[j].id) {
          this.allId[i - 1].active = true;
          j++;
        }
      }
    }
  }
};
</script>

<style>
  .user-all .unit {
    display: block;
    width: 60px;
    height: 60px;
    float: left;
    font-size: 16px;
    color: #fff;
    background-color: #c0c4cc;
    border-radius: 4px;
    margin-right: 10px;
    margin-bottom: 10px;
    text-align: center;
    line-height: 60px;
  }
  .user-all .unit.active {
    background-color: #67c23a;
  }
</style>
